<template>
    <div class="detail-container">
        <div class="detail-header">
            <div class="detail-header-title">{{title}}</div>
            <div class="detail-header-group">
                <div class="detail-header-icon-list" @click="listShow=!listShow"></div>
                <div class="detail-header-icon-close" @click="$router.go(-1)"></div>
            </div>
        </div>
        <div id="detail-gc-1" class="detail-gc-content"></div>
        <div class="detail-list" v-show="listShow" @click="listShow=false">
            <div class="detail-list-outer" @click.stop="()=>false">
                <table class="detail-list-table">
                    <thead>
                    <tr>
                        <th v-for="item in tData.th">{{item}}</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(item, i) in tData.td[0]">
                        <td>{{item}}</td>
                        <td>{{tData.td[1][i]}}</td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    import { ColorsMap } from './../../common/constants'
    export default {
        props: {
            chartData: {
                type: Object,
                default() {
                    return {}
                },
            },
        },
        data(){
            return {
                dividerColor: ColorsMap.dividerColor,
                fontColor: ColorsMap.fontColor,
                lineStyle: {
                    lineStyle: {
                        color: ColorsMap.dividerColor,
                        width: 1,
                        type: 'solid'
                    },
                },
                textStyle: {
                    textStyle: {
                        color: ColorsMap.fontColor,
                    },
                },
                chartObjs: {},
                optionObj: {},
                listShow: false,
                tData: {
                    th: [],
                    td: [],
                },
                title: '线索量走势图',
            }
        },
        mounted() {
            console.log(this.chartData)

            this.chartObjs[0] = this.$echarts.init(document.getElementById('detail-gc-1'))
            this.optionObj[0] = {
                grid: {
                    left: 20,
                    right: 30,
                    top: 30,
                    bottom: 50,
                    containLabel: true
                },
                color: ['#162C56'],
                ...this.textStyle,
                legend: {
                    bottom: 15,
                    itemGap: 15,
                    height: 30,
                    width: '80%',
                    itemWidth: 20,
                    itemHeight: 10,
                    textStyle: {
                        color: this.fontColor,
                    },
                },
                xAxis : [
                    {
                        type : 'category',
                        boundaryGap : false,
                        data : [],
                        axisTick: {
                            show: false,
                        },
                        axisLine: {
                            ...this.lineStyle,
                        },
                        splitLine: {
                            show: true,
                            interval: 1,
                            ...this.lineStyle,
                        },
                    },
                ],
                yAxis : [
                    {
                        type : 'value',
                        name: '万条',
                        // minInterval: 5,
                        axisLine: {
                            ...this.lineStyle,
                        },
                        axisTick: {
                            ...this.lineStyle,
                        },
                        splitLine: {
                            ...this.lineStyle,
                        },
                    },
                ],
                series : [
                    {
                        name:'总线索量',
                        type:'line',
                        stack: '总量',
                        areaStyle: {},
                        data: [],
                        lineStyle: {
                            color: '#6F9FD8',
                            width: 1,
                        },
                        symbol: 'none',
                    },
                ],
            }
            this.chartObjs[0].setOption(this.optionObj[0])
            let item = this.chartData
            let name= item.dy[item.index].name
            if (item.index===0) {
                name = '总线索量'
            }
            this.tData.th = [
                '日期',
                name,
            ]
            this.tData.td = [
                item.rawData.xAxisFull,
                item.dy[item.index].value
            ]
            this.title += `—${name}`
            console.log(this.tData)
            this.chartObjs[0].setOption({
                legend: {
                    data: [
                        name,
                    ],
                },
                xAxis: [
                    {
                        data: item.dx,
                    },
                ],
                series: [
                    {
                        name: name,
                        data: item.dy[item.index].value,
                    }
                ],
            })

        },
        methods: {

        }
    }
</script>
