<template>
    <component :is="full[query.td][query.id]" :chartData="chartData"></component>
</template>

<script>
    import Bus from './../common/bus'
    import Global_1 from './full/global-1'
    import Global_2 from './full/global-2'
    export default {
        components: {},
        data() {
            return {
                full: {
                    1: {
                        1: Global_1,
                        2: Global_2,
                    },
                    2: {
                        1: Global_1,
                        2: Global_2,
                    }
                },
                keysMap: {
                    detailData: 'bi-detail-local-data',
                },
                chartData: {},
                query: this.$route.query
            }
        },
        created(){
            this.getLocalData()
            console.log(this.query)
        },
        mounted() {
            Bus.$on('getOriginData', data=>{
                console.log(data)
            })
        },
        methods: {
            getLocalData(){
                let data = window.localStorage.getItem(this.keysMap.detailData)
                this.chartData = data && JSON.parse(data) || {}
            },
        },
    }
</script>

